.silde {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.1);
  left: 100%;
  z-index: 501;
  -webkit-transition: left 0.3s;
  transition: left 0.3s;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.silde ul {
  width: 70%;
  height: 100%;
  background: #eee;
  float: right;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.silde ul li {
  width: 50%;
  margin: 20px 0 5px;
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
}
.silde ul li a {
  display: block;
  height: 100%;
  width: 100%;
  color: #666;
  font-size: 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
